/*====================
    Slider Area 
======================*/

@media #{$sm-layout} {
  .bg_image--31 {
    background-position: 67% center !important;
  }
  .bg_image--32 {
    background-position: 27% center !important;
  }
}

.slider-fixed--height {
  min-height: 1080px;
  width: 100%;

  @media #{$laptop-device} {
    min-height: 900px;
  }
  @media #{$sm-layout} {
    min-height: auto;
    padding: 60px 0;
  }
  @media #{$md-layout} {
    min-height: auto;
    padding: 80px 0;
    padding-bottom: 60px;
  }
}

/*----------------------
Slider Default Style 
------------------------*/
.slide {
  .inner {
    padding-top: 100px;
    @media #{$sm-layout} {
      padding-top: 70px;
    }
    @media #{$large-mobile} {
      padding-top: 30px;
    }
    h1 {
      &.title {
        font-size: 125px;
        font-weight: 900;
        text-transform: uppercase;
        font-family: "Montserrat", sans-serif;
        font-weight: 900;
        line-height: 130px;
        @media #{$laptop-device} {
          font-size: 100px;
          line-height: 112px;
        }
        @media #{$lg-layout} {
          font-size: 90px;
          line-height: 103px;
        }
        @media #{$md-layout} {
          font-size: 70px;
          line-height: 82px;
          margin-bottom: 0;
        }
        @media #{$sm-layout} {
          font-size: 50px;
          line-height: 70px;
          margin-bottom: 0;
        }
        @media #{$large-mobile} {
          font-size: 40px;
          line-height: 50px;
          margin-bottom: 20px;
        }
      }
    }
  }
  &.slide-style-1 {
    .inner {
      padding-top: 100px;
      @media #{$sm-layout} {
        padding-top: 70px;
      }
      @media #{$large-mobile} {
        padding-top: 30px;
      }
      @media #{$md-layout} {
        padding-top: 50px;
      }
      h1 {
        &.title {
          @media #{$md-layout} {
            font-size: 70px;
            line-height: 82px;
            margin-bottom: 26px;
          }
        }
      }
    }
  }

  &.slide-style-2 {
    padding-top: 230px;
    padding-bottom: 284px;
    overflow: hidden;
    @media #{$laptop-device} {
      padding-top: 150px;
      padding-bottom: 150px;
    }

    &.fullscreen {
      min-height: 950px;
      @media #{$laptop-device} {
        padding-top: 160px;
        padding-bottom: 160px;
        min-height: auto;
      }
      @media #{$lg-layout} {
        min-height: auto;
      }
      @media #{$md-layout} {
        min-height: auto;
      }
      @media #{$sm-layout} {
        min-height: auto;
      }
    }

    &.slider-box-content {
      height: 650px;
      @media #{$lg-layout} {
        height: auto;
      }
      @media #{$md-layout} {
        height: auto;
      }
      @media #{$sm-layout} {
        height: auto;
      }
    }

    @media #{$lg-layout} {
      padding-top: 160px;
      padding-bottom: 170px;
      min-height: auto;
    }
    @media #{$md-layout} {
      padding-top: 150px;
      padding-bottom: 150px;
      min-height: auto;
    }

    @media #{$sm-layout} {
      padding-top: 150px;
      padding-bottom: 150px;
      min-height: auto;
    }
    .inner {
      position: relative;
      z-index: 2;
      @media #{$sm-layout} {
        padding-top: 0;
        text-align: center !important;
      }
      &.text-right {
        @media #{$sm-layout} {
          text-align: right !important;
        }
      }
      @media #{$md-layout} {
        padding-top: 30px;
      }
      @media #{$lg-layout} {
        padding-top: 30px;
      }
      > span {
        color: #fff;
        font-size: 15px;
        text-transform: uppercase;
        letter-spacing: 4px;
        display: block;
        margin-bottom: 9px;
      }

      h1 {
        &.title {
          color: #ffffff;
          font-size: 100px;
          font-weight: 900;

          @media #{$lg-layout} {
            font-size: 72px;
          }

          @media #{$md-layout} {
            font-size: 60px;
            margin-bottom: 18px;
          }
          @media #{$sm-layout} {
            font-size: 36px;
            line-height: 64px;
          }
        }
      }

      p {
        &.description {
          color: #c6c9d8;
          font-size: 24px;
          line-height: 40px;
          margin: 0;
          padding: 0 16%;
          font-weight: 300;
          @media #{$sm-layout} {
            padding: 0;
          }
          @media #{$md-layout} {
            padding: 0 10%;
          }
        }
      }
      .slide-btn {
        margin-top: 50px;
      }
      &.text-left {
        p {
          padding: 0 37% 0 0;
          @media #{$sm-layout} {
            padding: 0;
          }
          @media #{$md-layout} {
            padding: 0 3% 0 0;
          }
          @media #{$lg-layout} {
            padding: 0 20% 0 0;
          }
          @media #{$sm-layout} {
            padding: 0 0 0% 0;
          }
        }
      }
      &.text-right {
        p {
          padding: 0 0 0% 46%;
          @media #{$sm-layout} {
            padding: 0 0 0% 0;
          }
        }
      }
    }

    &.without-overlay {
      .inner {
        p {
          &.description {
            color: #fff;
            opacity: 0.7;
          }
        }
      }
    }

    &.slider-paralax {
      @media #{$laptop-device} {
        height: auto;
        padding-top: 150px;
        padding-bottom: 150px;
      }
    }
  }

  &.slider-style-3 {
    .inner {
      > span {
        color: #1f1f25;
        font-size: 15px;
        text-transform: uppercase;
        letter-spacing: 4px;
        display: block;
        margin-bottom: 20px;
        @media #{$sm-layout} {
          font-size: 13px;
          margin-bottom: 10px;
        }
      }
      h1 {
        &.title {
          color: #1f1f25;
          font-size: 75px;
          font-weight: 900;
          font-family: "Montserrat", sans-serif;
          line-height: 90px;
          text-transform: inherit;
          width: 70%;

          @media #{$lg-layout} {
            font-size: 70px;
          }

          @media #{$md-layout} {
            font-size: 60px;
            line-height: 77px;
            width: 85%;
          }

          @media #{$sm-layout} {
            font-size: 42px;
            line-height: 57px;
            width: 100%;
          }

          span {
            color: $theme-color;
          }
        }
      }
    }
  }
}
.slider-wrapper {
  &.color-white {
    .inner {
      p {
        &.description {
          color: #ffffff;
        }
      }
    }
  }
  .service-wrapper {
    @media #{$sm-layout} {
      margin-bottom: -20px;
    }
  }
}

.react-parallax {
  img {
    object-fit: cover;
  }
}

/*-------------------------
Personal Portfolio  
--------------------------*/
.personal-portfolio-slider {
  position: relative;
  @media #{$sm-layout} {
    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: #ffffff;
      opacity: 0.7;
    }
  }
  @media #{$md-layout} {
    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: #ffffff;
      opacity: 0.7;
    }
  }
  @media #{$lg-layout} {
    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: #ffffff;
      opacity: 0.7;
    }
  }
}
.slide {
  &.personal-portfolio-slider,
  &.designer-portfolio {
    &.slider-style-3 {
      @media #{$sm-layout} {
        padding: 100px 0;
      }
      @media #{$md-layout} {
        padding: 150px 0;
      }
      @media #{$lg-layout} {
        padding: 150px 0;
        min-width: auto;
        height: auto;
      }
      .inner {
        @media #{$sm-layout} {
          padding-top: 56px;
        }
        @media #{$md-layout} {
          padding-top: 76px;
        }
        h1 {
          &.title {
            @media #{$sm-layout} {
              font-size: 32px;
              line-height: 49px;
            }
            @media #{$md-layout} {
              font-size: 53px;
              line-height: 70px;
            }
            @media #{$lg-layout} {
              font-size: 58px;
              line-height: 75px;
            }
            @media #{$laptop-device} {
              font-size: 54px;
              line-height: 68px;
            }
          }
        }
        h2 {
          @media #{$laptop-device} {
            font-size: 54px;
            line-height: 1.3;
          }
          @media #{$sm-layout} {
            font-size: 32px;
            line-height: 1.3;
          }
          @media #{$md-layout} {
            font-size: 53px;
            line-height: 70px;
          }
          @media #{$lg-layout} {
            font-size: 58px;
            line-height: 75px;
          }
        }
      }
    }
  }
  &.designer-portfolio {
    height: 650px;

    @media #{$md-layout} {
      height: auto;
    }

    @media #{$sm-layout} {
      height: auto;
    }

    &.slider-style-3 {
      @media #{$md-layout} {
        height: auto;
        padding: 100px 0;
      }
      @media #{$sm-layout} {
        height: auto;
        padding: 100px 0;
      }
      .inner {
        padding: 0;
        h1 {
          &.title {
            width: 100%;
          }
        }
      }
      .designer-thumbnail {
        img {
          border-radius: 100%;
          padding: 0;
          border: 14px solid #fff;
          width: 394px;
          height: 394px;
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1),
            0 20px 40px rgba(255, 255, 255, 0.15);
          @media #{$large-mobile} {
            width: auto;
            height: auto;
          }
          @media #{$lg-layout} {
            width: auto;
            height: auto;
          }
        }
      }
    }
  }
}

/*--------------------------
    Slider Digital Agency  
----------------------------*/

.slider-digital-agency {
  a.rn-button-style--2 {
    color: #fff;
    border-color: #fff;
  }
}

/* ----------------------
    Slider Videi Bg 
------------------------*/

.slider-video-bg {
  position: relative;
  z-index: 2;
  .video-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .inner {
    position: relative;
    z-index: 2;
  }
  &[data-black-overlay] {
    &::before {
      z-index: 1;
    }
  }
  .video-inner {
    button {
      &.video-popup {
        @media #{$md-layout} {
          margin: 0;
          margin-top: 28px;
        }
        @media #{$sm-layout} {
          margin: 0;
          margin-top: 28px;
        }
        @media #{$sm-layout} {
          width: 70px;
          height: 70px;
          margin: 0 auto;
          margin-top: 30px;
        }
      }
    }
  }
}
.slide {
  &.slide-style-2 {
    &.slider-video-bg {
      .inner {
        .title {
          font-size: 60px;
          line-height: 80px;
          margin-bottom: 20px;
          @media #{$sm-layout} {
            font-size: 44px;
            line-height: 60px;
            margin-bottom: 13px;
          }
          @media #{$large-mobile} {
            font-size: 36px;
            line-height: 53px;
          }
        }
      }
    }
  }
}

/* ---------------------------
    Slider Creative Agency
------------------------------*/

.slider-creative-agency {
  .slider-paralax {
    @media #{$md-layout} {
      height: auto;
    }
    @media #{$sm-layout} {
      height: auto;
    }
  }
  &.with-particles {
    position: relative;
    .frame-layout__particles {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      canvas {
        position: absolute;
        z-index: 1;
      }
    }
    .inner {
      position: relative;
      z-index: 4;
    }
  }
}

/*-------------------------
For Slider Service 
---------------------------*/

.slide {
  .service {
    margin-top: 50px;
    @media #{$lg-layout} {
      margin-top: 20px;
    }

    @media #{$laptop-device} {
      margin-top: 0px;
    }
    @media #{$md-layout} {
