/*=======================
    Slider Paralax 
=========================*/

.slider-paralax,
.rn-paralax {
  height: 950px;
  @media #{$sm-layout} {
    height: auto;
  }
  @media #{$laptop-device} {
    height: auto;
    padding-top: 150px;
    padding-bottom: 150px;
  }
  @media #{$md-layout} {
    height: auto;
  }
  @media #{$lg-layout} {
    height: auto;
  }
}

/*--------------------------
Slick Animation For Slider  
-----------------------------*/
.slick-slide {
  &.slick-current {
    .slide {
      &.slide-style-2 {
        .inner {
          h1 {
            &.title {
              -webkit-animation: 800ms ease-in-out 0s normal none 1 running
                customOne;
              animation: 800ms ease-in-out 0s normal none 1 running customOne;
            }
          }
          p {
            &.description {
              -webkit-animation: 1000ms ease-in-out 0s normal none 1 running
                customOne;
              animation: 1000ms ease-in-out 0s normal none 1 running customOne;
            }
          }
          .slide-btn {
            -webkit-animation: 1200ms ease-in-out 0s normal none 1 running
              customOne;
            animation: 1200ms ease-in-out 0s normal none 1 running customOne;
          }
        }
      }
    }
  }
}

.bg_image--16 {
  @media #{$sm-layout} {
    background-position: 71% center;
  }
}

/*--------------------------------------
Slider Default Button And Dot STyle
---------------------------------------*/

.slider-activation {
  position: relative;
  .slick-dots {
    bottom: 60px;
    @media #{$sm-layout} {
      bottom: 88px;
    }
  }
  button {
    &.slick-arrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      transition: 0.5s;
      opacity: 1;
      width: 68px;
      height: 68px;
      border: 2px solid rgba(255, 255, 255, 0.2);
      border-radius: 100%;

      @media #{$md-layout} {
        display: none !important;
      }

      @media #{$lg-layout} {
        display: none !important;
      }
      @media #{$sm-layout} {
        display: none !important;
      }
      &:hover {
        background: #f9004d;
        border-color: #f9004d;
      }
      &::before {
        line-height: 1;
        opacity: 0.4;
        position: relative;
        top: -2px;
        transition: 0.5s;
        z-index: 2;
      }
      &.slick-prev {
        left: 50px;
        @media #{$sm-layout} {
          left: 10px;
        }

        &::before {
          content: url(/assets/images/icons/slick-arrow-left-small.png);
        }
        &:hover {
          left: 30px;
          @media #{$sm-layout} {
            left: 20px;
          }
          &::before {
            opacity: 1;
          }
        }
      }
      &.slick-next {
        left: auto;
        right: 50px;
        @media #{$sm-layout} {
          right: 10px;
        }
        &::before {
          content: url(/assets/images/icons/slick-arrow-right-small.png);
        }
        &:hover {
          right: 30px;
          @media #{$sm-layout} {
            right: 20px;
          }
          &::before {
            opacity: 1;
          }
        }
      }
    }
  }
}

/*--------------------
Custom Animation  
----------------------*/

@-webkit-keyframes customOne {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 50%, 0);
    transform: translate3d(0, 50%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes customOne {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 50%, 0);
    transform: translate3d(0, 50%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.customOne {
  -webkit-animation-name: customOne;
  animation-name: customOne;
}

.theme-gradient {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

.thumbnail img {
  border-radius: 5px;
  box-shadow: 0 25px 65px rgba(0, 0, 0, 0.1);
}

/*---------------------
    Box Model  
----------------------*/

.box-model .service-wrapper {
  background-image: url(/assets/images/bg/bg-image-1.jpg);
  padding: 10px 50px;
  border-radius: 6px;
  position: relative;
}
.box-model .service-wrapper .service {
  margin: 0;
}
.box-model .service-wrapper .service .icon {
  margin: auto;
  text-align: center;
}
.box-model .service-wrapper .service .content {
  text-align: center;
}
.box-model .service-wrapper:before {
  width: 90%;
  height: 80%;
  position: absolute;
  left: 0;
  bottom: -30px;
  content: "";
  background-color: #f81f01;
  z-index: -1;
  margin: auto;
  right: 0;
  border-radius: 20%;
  background-image: linear-gradient(145deg, #f81f01 0%, #ee076e 100%);
  filter: blur(30px);
}
.box-model .inner {
  text-align: center;
  margin-bottom: 60px;
}
